import Swiper, { Autoplay, FreeMode } from 'swiper';
import 'swiper/scss';
import 'ScssComponents/_logos.scss';

Swiper.use([Autoplay, FreeMode]);

const logoSlider = () => {
	const $logoSlider = document.querySelector('.logoSlider');

	if (typeof $logoSlider !== 'undefined' && $logoSlider != null) {
		const logoSwiper = new Swiper($logoSlider, {
			slidesPerView: 'auto',
			a11y: true,
			loop: true,
			speed: 4000,
			autoplay: {
				delay: 1,
			},
			breakpoints: {
				1024: {
					loop: false,
					autoplay: false,
				},
			},
		});
	}
};

export default logoSlider;
