import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';

const AnimEl = () => {
	gsap.registerPlugin(ScrollTrigger);
	const $fadeEl = document.querySelectorAll('.fadeEl');
	const $scaleEl = document.querySelectorAll('.scaleEl');

	if (typeof $fadeEl !== 'undefined' && $fadeEl.length) {
		// console.log($fadeEl);

		gsap.set($fadeEl, {
			y: 100,
			opacity: 0,
		});

		setTimeout(() => {
			ScrollTrigger.batch($fadeEl, {
				start: '15% bottom',
				onEnter: (batch) => {
					gsap.to(batch, {
						opacity: 1,
						y: 0,
						stagger: 0.05,
						ease: 'none',
					});
				},
			});
		}, 100);
	}

	if (typeof $scaleEl !== 'undefined' && $scaleEl.length) {
		gsap.set($scaleEl, {
			scaleX: 0.3,
			scaleY: 0.3,
			opacity: 0,
		});

		setTimeout(() => {
			ScrollTrigger.batch($scaleEl, {
				start: '75% bottom',
				onEnter: (batch) => {
					gsap.to(batch, {
						opacity: 1,
						scaleX: 1,
						scaleY: 1,
						duration: 0.5,
						stagger: 0.1,
						ease: 'power2.ease',
					});
				},
			});
		}, 100);
	}
};
export default AnimEl;
