import { onWindowResize } from 'utils/index';
import 'ScssComponents/_work_space.scss';
import gsap from 'gsap';

const WorkSpace = () => {
	const $workSpaceSection = document.querySelector('.workSpaceSection');

	if (typeof $workSpaceSection !== 'undefined' && $workSpaceSection != null) {
		let elRect;
		let mousePos = { x: 0, y: 0 };
		let mouseEnterEvent = false;

		const rectFunc = (e) => {
			elRect = $workSpaceSection.getBoundingClientRect();
		};

		onWindowResize(rectFunc);

		const $orbits = document.querySelector('[data-name="orbits"]');
		const $otherElements = document.querySelector(
			'[data-name="other_elements"]',
		);
		const $otherElementsInArray = $workSpaceSection.querySelectorAll(
			'[data-name="other_elements_in"]',
		);
		const $elCenter = $workSpaceSection.querySelector(
			'[data-name="center_element"]',
		);
		const $orbit1 = $workSpaceSection.querySelector('[data-name="orbit_1"]');
		const $orbit2 = $workSpaceSection.querySelector('[data-name="orbit_2"]');
		const $orbit3 = $workSpaceSection.querySelector('[data-name="orbit_3"]');
		const $phpEl = $workSpaceSection.querySelector('[data-name="php_el_3"]');
		const $nextEl = $workSpaceSection.querySelector('[data-name="next_el_3"]');
		const $nodeEl = $workSpaceSection.querySelector('[data-name="node_el_4"]');
		const $reactEl = $workSpaceSection.querySelector(
			'[data-name="react_el_4"]',
		);
		const $decorsArray = $workSpaceSection.querySelectorAll(
			'[data-name="decor_el_6"]',
		);
		const $wordpressEl = $workSpaceSection.querySelector(
			'[data-name="wordpress_el_3"]',
		);
		const $wooEl = $workSpaceSection.querySelector('[data-name="woo_el_4"]');
		const $decors2Array = $workSpaceSection.querySelectorAll('[data-name="decor_el_5"]');

		const rotateTl = gsap.timeline({
			paused: true,
			repeat: -1,
			defaults: {
				ease: 'none',
				transformOrigin: '50% 50%',
				duration: 200,
			},
		});

		rotateTl
			.addLabel('start')
			.to(
				$orbits,
				{
					rotation: -360,
				},
				'start',
			)
			.to(
				$otherElements,
				{
					rotation: -360,
				},
				'start',
			)
			.to(
				$otherElementsInArray,
				{
					rotation: 360,
				},
				'start',
			);

		$workSpaceSection.addEventListener('mousemove', (e) => {
			mousePos.moved = true;
			mousePos.x = e.clientX / window.innerWidth;
			mousePos.y = e.clientY / window.innerHeight;
		});

		const parallaxInit = (target, durationTime, movement, onlyVertical) => {
			gsap.to(target, {
				x: onlyVertical ? 0 : mousePos.x * movement,
				y: mousePos.y * movement,
				duration: durationTime,
				ease: 'sine',
			});
		};

		const tickerFunc = () => {
			if (mousePos.moved) {
				// console.log('123');
				parallaxInit($elCenter, 1, 1);
				// parallaxInit($orbit1, 0.7, 2);
				parallaxInit($orbit2, 0.7, -4, true);
				parallaxInit($orbit3, 0.7, 3, true);
				parallaxInit($phpEl, 0.3, 9);
				parallaxInit($nextEl, 0.3, 15);
				parallaxInit($wordpressEl, 0.3, 5);
				parallaxInit($wooEl, 0.3, -6);
				parallaxInit($reactEl, 0.3, -9);
				parallaxInit($nodeEl, 0.3, -14);
				$decorsArray.forEach((el, index) => {
					parallaxInit(el, 0.3, index + 1);
				});
				$decors2Array.forEach((el, index) => {
					parallaxInit(el, 0.3, index * -2 - 3);
				});
			}
			mousePos.moved = false;
		};

		$workSpaceSection.addEventListener('mouseenter', () => {
			if (!mouseEnterEvent) {
				mouseEnterEvent = true;
				gsap.ticker.add(tickerFunc);
				// rotateTl.play();
			}
		});

		$workSpaceSection.addEventListener('mouseleave', () => {
			if (mouseEnterEvent) {
				mouseEnterEvent = false;
				gsap.ticker.remove(tickerFunc);
				// rotateTl.pause();
			}
		});
	}
};

export default WorkSpace;
