import { onWindowResize } from 'utils/index';
import { GLOBAL_VARS } from 'utils/constants';
import gsap from 'gsap';
// import 'ScssComponents/_hero.scss';

const Hero = () => {
	// console.log('hero');
	const $wordsWrap = document.querySelectorAll('.heroWordWrap');
	const $words = document.querySelectorAll('.heroWord');
	const $videos = document.querySelectorAll('.heroVideo');
	const wordsArr = [];
	let isMobile;
	const videoDuration = 5;
	const easingValue = 'power2.out';
	const ACTIVE_VIDEO_CLASS = 'hero_video__in--active';

	const checkIsMobile = () => {
		isMobile = window.innerWidth < GLOBAL_VARS.mediaPoint2;
	};

	onWindowResize(checkIsMobile);

	if ($words.length) {
		$words.forEach(el => {
			let obj = {
				el,
				title: el.querySelector('.heroWordText'),
				titleIn: el.querySelector('.heroWordTextIn'),
				dot: el.querySelector('.heroWordDot'),
			};
			wordsArr.push(obj);
		});
		const tl = gsap.timeline({
			paused: false,
			delay: 0,
			repeat: -1,
			repeatDelay: 0,
			ease: 'power2.out',
		});

		gsap.set($wordsWrap, {
			opacity: 1,
		});

		if (!isMobile) {
			$videos.forEach(video => {
				const videoSource = video.dataset.src;
				video.setAttribute('src', videoSource);
			});
		}

		wordsArr.forEach((item, index) => {
			tl
				.fromTo([item.title, item.titleIn, item.dot], {
					yPercent: gsap.utils.wrap([-105, 105, 0]),
					xPercent: gsap.utils.wrap([0, 0, 150]),
					opacity: gsap.utils.wrap([1, 1, 0]),
				}, {
					yPercent: gsap.utils.wrap([0, 0, 0]),
					xPercent: gsap.utils.wrap([0, 0, 0]),
					opacity: gsap.utils.wrap([1, 1, 1]),
					duration: 0.8,
					ease: easingValue,
					onStart: () => {
						if (!isMobile) {
							$videos[index].play();
							$videos[index].classList.add(ACTIVE_VIDEO_CLASS);
						}
					},
				})
				.to([item.title, item.titleIn, item.dot], {
					yPercent: gsap.utils.wrap([-105, 105, 0]),
					xPercent: gsap.utils.wrap([0, 0, 200]),
					opacity: gsap.utils.wrap([1, 1, 0]),
					duration: 0.6,
					ease: easingValue,
					onComplete: () => {
						if (!isMobile) {
							$videos[index].pause();
							$videos[index].currentTime = 0;
							$videos[index].classList.remove(ACTIVE_VIDEO_CLASS);
						}
					},
				}, `+=${videoDuration}`);
		});
	}
};

export default Hero;
