import { GLOBAL_VARS } from 'utils/constants';
import { documentReady, pageLoad } from 'utils';

// components
import testimonialsSection from 'components/testimonialsSection/testimonialsSection';
import WhatToRead from 'components/WhatToRead';
import OurProducts from 'components/OurProducts';
import FeaturesSection from 'components/FeaturesSection';
import WorkSpace from 'components/WorkSpace';
import LatestArticles from 'components/LatestArticles';
import DevPartner from 'components/DevPartner';
import { ShowcasesSlider } from 'components/showcase';
import Hero from '../components/Hero';
import Blog from '../components/Blog';
import ContactUs from '../components/ContactUs';

import AnimEl from '../components/AnimEl';

export default class IndexPage {
	constructor() {
		this.init = this.init.bind(this);
	}

	loadFunc() {
		AnimEl();
		testimonialsSection();
		OurProducts();
		FeaturesSection();
		WorkSpace();
		DevPartner();
		WhatToRead();
		Blog();
		LatestArticles();
		WhatToRead();
		const showcaseSlider = new ShowcasesSlider({
			sliderId: '#showcaseSlider',
		});
	}

	init() {
		Hero();
		setTimeout(() => {
			this.loadFunc();
		});
	}
}
