import Swiper, { Navigation, Pagination } from 'swiper';
import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import Observer from 'gsap/Observer';
import { GLOBAL_VARS } from 'utils/constants';
import { getWindowSize } from 'utils';
import ShowcasesItem from './ShowcaseItem';

import 'ScssComponents/_showcases.scss';

Swiper.use([Navigation, Pagination]);

gsap.registerPlugin(Observer);

class ShowcasesSlider {
	get SELECTORS() {
		return {
			component: '.showcaseSlider',
			showcaseItem: '.showcaseItem',
			currentIndexSlide: '.showcaseCurrent',
			prevArrow: '.showcasePrevArrow',
			nextArrow: '.showcaseNextArrow',
			sliderDots: '.showcaseDots',
			sliderDot: '.showcaseDot',
			sliderDotIn: '.showcaseDotIn',
		};
	}

	constructor({
		sliderId,
	}) {
		this.$component = document.querySelector(sliderId);

		if (!this.$component) {
			throw new Error('Element not found on page');
		}

		this.itemsInstance = [];
		this.sliderInstance = null;
		this.isAnimationActive = false;
		this.autoPlayInterval = null;

		this.AUTOPLAY_DELAY = 3800;

		this.onSlideChange = this.onSlideChange.bind(this);
		this.createDotSlider = this.createDotSlider.bind(this);

		this.init();
	}

	createDotSlider() {
		const FIRST_SLIDE_DURATION = 4.4;
		const DEFAULT_SLIDE_DURATION = 5.1;

		this.dotsTimeline = gsap.timeline({
			paused: false,
			repeat: -1,
		});

		this.$component.querySelectorAll(this.SELECTORS.sliderDotIn).forEach((item, index) => {
			this.dotsTimeline
				.addLabel(`dot_${index}`)
				.fromTo(item, {
					xPercent: -101,
				}, {
					xPercent: 0,
					ease: 'none',
					duration: index === 0 ? FIRST_SLIDE_DURATION : DEFAULT_SLIDE_DURATION,
				});
		});
	}

	createItemsInstance() {
		const $sliderItems = this.$component.querySelectorAll(this.SELECTORS.showcaseItem);

		if (!$sliderItems.length) return;

		$sliderItems.forEach($item => {
			this.itemsInstance.push(new ShowcasesItem({
				rootElement: $item,
			}));
		});
	}

	onSlideChange(slider) {
		if (this.isAnimationActive) return;

		const { previousIndex, realIndex } = slider;
		this.dotsTimeline.seek(`dot_${realIndex}`);

		const timeline = gsap.timeline({
			onStart: () => {
				this.isAnimationActive = true;
				this.updateCounter(realIndex);
			},
			onComplete: () => {
				this.isAnimationActive = false;
				this.setAutoplayInterval();
			},
		});

		const ENTER_ANIMATION_LABEL = this.windowWidth >= GLOBAL_VARS.mediaPoint1 ? 'start' : 'start+=.2';

		timeline
			.addLabel('start')
			.add(this.itemsInstance[previousIndex].leaveAnimation(), 'start')
			.add(this.itemsInstance[realIndex].enterAnimation(), ENTER_ANIMATION_LABEL);

		timeline.play();
	}

	clearAutoplayInterval() {
		clearInterval(this.autoPlayInterval);
	}

	setAutoplayInterval() {
		this.autoPlayInterval = setInterval(() => {
			this.autoPlayHandler();
		}, this.AUTOPLAY_DELAY);
	}

	slidePrevHandler() {
		if (this.isAnimationActive) return;

		this.clearAutoplayInterval();

		if (this.sliderInstance.realIndex === 0) {
			this.sliderInstance.slideTo(this.sliderInstance.slides.length - 1);
		} else {
			this.sliderInstance.slidePrev();
		}
	}

	slideNextHandler() {
		if (this.isAnimationActive) return;

		this.clearAutoplayInterval();

		if (this.sliderInstance.realIndex === this.sliderInstance.slides.length - 1) {
			this.sliderInstance.slideTo(0);
		} else {
			this.sliderInstance.slideNext();
		}
	}

	autoPlayHandler() {
		this.slideNextHandler();
	}

	updateCounter(slideIndex) {
		if (!this.$currentIndexSlide) return;

		const updatedIndex = slideIndex + 1;
		this.$currentIndexSlide.innerHTML = updatedIndex >= 10 ? updatedIndex : `0${updatedIndex}`;
	}

	initSwiper() {
		this.$currentIndexSlide = this.$component.querySelector(this.SELECTORS.currentIndexSlide);
		const $totalAmountSlides = this.$component.querySelector('.showcaseTotal');

		const $prevEl = this.$component.querySelector(this.SELECTORS.prevArrow);
		const $nextEl = this.$component.querySelector(this.SELECTORS.nextArrow);
		const $dotsEl = this.$component.querySelector(this.SELECTORS.sliderDots);
		let $dotEl;

		this.sliderInstance = new Swiper(this.$component, {
			speed: 0,
			loop: false,
			allowTouchMove: false,
			centeredSlides: false,

			pagination: {
				el: $dotsEl,
				renderBullet: (index, className) => {
					return `<div class="${className} showcaseDot" data-index=${index}><div class="showcase__dot_in showcaseDotIn"></div></div>`;
				},
			},

			on: {
				init: (slider) => {
					const index = slider.slides.length;
					$totalAmountSlides.innerHTML = index >= 10 ? index : `0${index}`;
					$dotEl = this.$component.querySelectorAll(this.SELECTORS.sliderDot);
					this.createDotSlider();
				},
				slideChange: this.onSlideChange,
			},
		});

		$prevEl.addEventListener('click', () => {
			this.slidePrevHandler();
		});

		$nextEl.addEventListener('click', () => {
			this.slideNextHandler();
		});

		$dotEl.forEach(item => {
			item.addEventListener('click', () => {
				if (this.isAnimationActive) return;
				const dotIndex = item.dataset.index;
				if (this.sliderInstance.realIndex === dotIndex) return;
				// this.dotsTimeline.seek(`dot_${dotIndex}`);
				this.clearAutoplayInterval();
				this.sliderInstance.slideTo(dotIndex);
			});
		});

		Observer.create({
			target: this.$component,
			type: 'touch',
			tolerance: 50,
			onLeft: () => this.slideNextHandler(),
			onRight: () => this.slidePrevHandler(),
		});
	}

	onViewHandler() {
		const onLeave = () => {
			this.clearAutoplayInterval();
		};

		const onEnter = () => {
			this.setAutoplayInterval();
		};

		ScrollTrigger.batch(this.$component, {
			start: '15% bottom',
			onEnter: () => onEnter(),
			onEnterBack: () => onEnter(),
			onLeave: () => onLeave(),
			onLeaveBack: () => onLeave(),
		});
	}

	init() {
		this.createItemsInstance();
		this.initSwiper();
		this.onViewHandler();

		const { windowWidth } = getWindowSize();

		this.windowWidth = windowWidth;
	}
}

export default ShowcasesSlider;
