import 'ScssComponents/_testimonial_client.scss';
import TestimonialClient from './TestimonialClient';

const testimonialClients = () => {
	const $testimonialClients = document.querySelectorAll('.testimonialClient');

	if (!$testimonialClients.length) return;

	$testimonialClients.forEach(($client) => {
		const clientInstance = new TestimonialClient({
			rootNode: $client,
		});
	});
};

export default testimonialClients;
