import Swiper, { Navigation, EffectFade, Pagination } from 'swiper';
import 'swiper/css/effect-fade';
import 'ScssComponents/_testimonial_slider.scss';
import 'ScssComponents/_slider_controls.scss';
import { getWindowSize } from 'utils';
import { GLOBAL_VARS } from 'utils/constants';

Swiper.use([Navigation, EffectFade, Pagination]);

const testimonialsSlider = () => {
	const $testimonialSliders = document.querySelectorAll('.testimonialSlider');
	const $currentSlideIndex = document.querySelector(
		'.testimonial_slider__pagination_count--current',
	);
	const $totalSlideIndex = document.querySelector(
		'.testimonial_slider__pagination_count--total',
	);

	if (!$testimonialSliders.length) return;

	const { windowWidth } = getWindowSize();

	$testimonialSliders.forEach(($slider) => {
		const $prevButton = $slider.querySelector('.swiper-button-prev');
		const $nextButton = $slider.querySelector('.swiper-button-next');
		const $dots = $slider.parentElement.querySelector('.testimonial_slider__pagination_dots');

		const allowTouchMove = !(windowWidth >= GLOBAL_VARS.mediaPoint1);

		const swiperInstance = new Swiper($slider, {
			effect: 'fade',
			fadeEffect: { crossFade: true },
			slidesPerView: 1,
			slidesPerGroup: 1,
			allowTouchMove,
			loop: true,
			navigation: {
				prevEl: $prevButton,
				nextEl: $nextButton,
			},
			pagination: {
				el: $dots,
				type: 'progressbar',
				clickable: true,
			},
			on: {
				init: (slider) => {
					if ($totalSlideIndex) {
						const index = slider.slides.length - 2; // -2 for real value with "loop: true"
						$totalSlideIndex.innerHTML = index >= 10 ? index : `0${index}`;
					}
				},
				slideChange: (slider) => {
					if ($currentSlideIndex) {
						const index = slider.realIndex + 1;
						$currentSlideIndex.innerHTML = index >= 10 ? index : `0${index}`;
					}
				},
			},
		});
	});
};

export default testimonialsSlider;
