import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import './DevPartnerCube';

import 'ScssComponents/_dev_partner.scss';

const DevPartner = () => {
	const $devCube = document.querySelectorAll('.devCube');
	const $section = document.querySelector('.dev_partner');
	const devArr = [];
	$devCube.forEach(el => {
		const obj = {
			el,
			bg: el.querySelector('.dev_partner_cube__img_wrap'),
			bgIn: el.querySelector('.dev_partner_cube__img_wrap_in'),
			icon: el.querySelector('.dev_partner_cube__icon_wrap'),
			text: el.querySelector('.dev_partner_cube__text'),
		};

		devArr.push(obj);
	});

	devArr.forEach(el => {
		const tl = gsap.timeline({
			paused: false,
			scrollTrigger: {
				trigger: $section,
				start: 'top 25%',
			},
		});

		tl
			.addLabel('start')
			.fromTo([el.bg, el.bgIn], {
				opacity: 0,
				xPercent: gsap.utils.wrap([100, -100]),
			}, {
				opacity: 1,
				xPercent: gsap.utils.wrap([0, 0]),
				duration: 1,
				ease: 'power2.out',
			}, 'start');

		if (el.icon) {
			tl
				.fromTo([el.icon, el.text], {
					opacity: 0,
					x: 20,
				}, {
					opacity: 1,
					x: 0,
					duration: 1,
					ease: 'power2.out',
				}, 'start+=.3');
		}
	});
};

export default DevPartner;
