import gsap from 'gsap';
import { getWindowSize, onWindowResize } from 'utils';
import { GLOBAL_VARS } from 'utils/constants';

class ShowcasesItem {
	get SELECTORS() {
		return {
			image: '.showcaseItemImage',
			content: '.showcaseItemContent',
			bgDecor: '.showcaseItemBgDecor',
			contentInnerItem: '.showcaseItemContentEl',
		};
	}

	constructor({
		rootElement,
	}) {
		this.$component = rootElement;

		if (!this.$component) {
			throw new Error('Element not found on page');
		}

		this.$image = this.$component.querySelector(this.SELECTORS.image);
		this.$content = this.$component.querySelector(this.SELECTORS.content);
		this.$bgDecors = this.$component.querySelectorAll(this.SELECTORS.bgDecor);
		this.$contentInnerEl = this.$component.querySelectorAll(this.SELECTORS.contentInnerItem);

		this.windowWidth = 0;
		this.DELAY_RESET_ANIMATION = 1;
		this.TIMELINE_TIME_SCALE = 1.3;

		this.ANIM_CONFIG = {
			baseEase: 'power2.out',
		};

		this.init();
	}

	resetComponentStyle() {
		gsap.set(this.$component, {
			zIndex: 'auto',
			delay: this.DELAY_RESET_ANIMATION,
		});
	}

	createDesktopLeaveAnimation() {
		const timeline = gsap.timeline({
			onComplete: () => {
				this.resetComponentStyle();

				gsap.set(this.$content, {
					clearProps: 'all',
				});
			},
		});

		timeline
			.addLabel('start')
			.to(this.$component, {
				zIndex: 1,
				duration: 0.01,
			})
			.fromTo(this.$content, {
				opacity: 1,
			}, {
				opacity: 0,
				duration: 1,
			}, 'start');

		timeline.timeScale(this.TIMELINE_TIME_SCALE);

		return timeline;
	}

	createDesktopEnterAnimation() {
		const timeline = gsap.timeline();

		timeline
			.addLabel('start')
			.to(this.$component, {
				zIndex: 5,
				duration: 0.01,
			})
			.fromTo(this.$bgDecors, {
				yPercent: gsap.utils.wrap([100, -100]),
			}, {
				yPercent: gsap.utils.wrap([0, 0]),
				ease: 'power3.out',
				duration: 1.3,
			}, 'start')
			.fromTo([this.$image, this.$content], {
				y: gsap.utils.wrap(['0rem', '0rem']),
				opacity: 0,
			}, {
				y: gsap.utils.wrap(['3rem', '-3rem']),
				opacity: 1,
				ease: this.ANIM_CONFIG.baseEase,
				stagger: 0.4,
				duration: 0.6,
			}, 'start+=.8');

		timeline.timeScale(this.TIMELINE_TIME_SCALE);

		return timeline;
	}

	createMobileLeaveAnimation() {
		const timeline = gsap.timeline({
			onComplete: () => {
				this.resetComponentStyle();

				gsap.set(this.$contentInnerEl, {
					clearProps: 'all',
					delay: this.DELAY_RESET_ANIMATION,
				});
			},
		});

		timeline
			.addLabel('start')
			.to(this.$component, {
				zIndex: 1,
				duration: 0.01,
			})
			.fromTo(this.$contentInnerEl, {
				opacity: 1,
			}, {
				opacity: 0,
				y: '-2rem',
				duration: 0.4,
				ease: this.ANIM_CONFIG.baseEase,
			}, 'start');

		return timeline;
	}

	createMobileEnterAnimation() {
		const timeline = gsap.timeline();

		timeline
			.addLabel('start')
			.to(this.$component, {
				zIndex: 5,
				duration: 0.01,
			})
			.fromTo(this.$bgDecors[0], {
				yPercent: -100,
			}, {
				yPercent: 0,
				ease: 'power3.out',
				duration: 1,
			}, 'start')
			.fromTo(this.$contentInnerEl, {
				y: '3rem',
				opacity: 0,
			}, {
				y: '0rem',
				opacity: 1,
				stagger: 0.4,
				duration: 0.6,
				ease: this.ANIM_CONFIG.baseEase,
			}, 'start+=.5');

		return timeline;
	}

	leaveAnimation() {
		if (this.windowWidth >= GLOBAL_VARS.mediaPoint1) return this.createDesktopLeaveAnimation();

		return this.createMobileLeaveAnimation();
	}

	enterAnimation() {
		if (this.windowWidth >= GLOBAL_VARS.mediaPoint1) return this.createDesktopEnterAnimation();

		return this.createMobileEnterAnimation();
	}

	onResize() {
		const { windowWidth } = getWindowSize();

		this.windowWidth = windowWidth;
	}

	init() {
		onWindowResize(() => {
			this.onResize();
		});
	}
}

export default ShowcasesItem;
